import React, { useEffect, useState } from "react";
import { Wrapper } from "./Styles"
import { Row, Col, message } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { translate } from "react-polyglot";
import Banner from "../../components/banner/Banner";
import CardCarousel from "../../components/card/CardCarousel";
import { getService } from "../../services";
import { DEFAULT_IMAGE_COVER, URL_S3 } from "../../constants";
import Loader from "../../components/loader/";
import Tags from "../../components/tags/Tags";
import { CHANGE_ENTITY } from "../../constants/actionTypes";
import SearchField from "../../components/search-field/SearchField";



const PackCategories = ({ t, id, reference = "categories-packs", ...props }) => {
    const dispatch = useDispatch();
    const [record, setRecord] = useState([]);
    const [loading, setLoading] = useState(true);
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const entity_id = useSelector(({ auth }) => {
        return auth.entity_id;
    });
    const getEntity = () => {
        const service = getService("entities");
        setLoading(true);
        service.get(entity_id)
          .then(entity => {
            setLoading(false);
            dispatch({
              type: CHANGE_ENTITY,
              entity
            });
          })
          .catch(err => {
            setLoading(false);
          });
      }
      useEffect(() => {
        if (entity_id)
          getEntity();
      }, []);
    let image_cover = record.image_cover ? `${URL_S3}/${record.image_cover}` : DEFAULT_IMAGE_COVER;
    const getData = () => {
        if (reference) {
            const service = getService(reference);
            setLoading(true);
            service.get(id)
                .then(record => {
                    setRecord(record)
                    setLoading(false);
                })
                .catch(err => {
                    message.error(err.message)
                    setLoading(false);
                })

        }
    }
    useEffect(() => {
        getData();
    }, [id])
    if (loading) return <Loader />;
    return (
        <Wrapper>
            <Banner dataSource={[{
                title: (record[`name_${locale}`]),
                 description:(t("pack.text.categories")),
                // image_path:cover
                image_path: image_cover
            }]} />
             <Row className="section section-search" type="flex" justify="center" align="center" gutter={10}>
                <Col className="section-container-search" xl={10} lg={16} md={16} xs={22} >
                    <SearchField
                        placeholder={t("search.Text.Input")}
                        reference="packs"
                        open={false}
                        source={`name_${locale}`}
                    />
                </Col>
            </Row>
            <Row ClassName="section container-section"
                align="top" justify="center" type="flex"
                gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
                <Col className="section section-category" xl={20} lg={24} md={24} xs={24}>
                    <div className="categories">
                        <Tags
                            filterDefaultValues={{
                                entity_id
                            }}
                            reference="categories-packs"
            /* dataSource={categories} */ />
                    </div>
                </Col>
                <Col ClassName="container-card" xl={20} lg={24} md={24} xs={24}>
                    <CardCarousel
                        filterDefaultValues={{
                            entity_id,
                            category_pack_id: id
                        }}
                        reference="packs"
                    />
                </Col>
                <Row
                    className="section-container"
                    justify="center" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>

                </Row>
            </Row>
        </Wrapper>
    );
}
export default translate()(PackCategories);